.post-card-tags.post-card-tags-inline {
    display: inline-block;
    text-transform: lowercase;
    background: #eee;
    border-radius: 3px 0 0 3px;
    color: #999;
    display: inline-block;
    height: 26px;
    line-height: 26px;
    padding: 0 16px;
    position: relative;
    margin: 0 10px 10px 0;
    text-decoration: none;
  }
  
  .post-card-tags.post-card-tags-inline::after {
    background: #fff;
    border-bottom: 13px solid transparent;
    border-left: 10px solid #eee;
    border-top: 13px solid transparent;
    content: '';
    position: absolute;
    right: 0;
    top: 0;
  }
  
.post-feed .post-card-content-link {
  box-shadow: none;
}

.post-full-content .post-feed {
  margin-left: -100px;
  margin-right: -100px;
  font-size: 1.5rem;  
  line-height: 1.6em;
}

.post-full-content .post-feed h2 {
  font-size: 2rem;
}

@media only screen and (max-width: 768px) {
  .post-full-content .post-feed {
    margin-left: 0;
    margin-right: 0;
  }
}